import React from 'react'
import './intro.css'
import Floadtingdiv from "../Floatingdiv/Floatingdiv"

import { themeContext } from "../../Context";
import { useContext } from "react";

import { motion } from 'framer-motion'

import Github from '../../img/github.png'
import Linked from '../../img/linkedin.png'
import Instagram from '../../img/instagram.png'

import Vector1 from '../../img/Vector1.png'
import Vector2 from '../../img/Vector2.png'
import Boy from '../../img/boy.png'
import thumbup from '../../img/thumbup.png'
import crown from '../../img/crown.png'
import glassesimoji from '../../img/glassesimoji.png'

const Intro = () => {

    const trasition = {duration:2, type:'spring;'}

    const theme = useContext(themeContext);
    const darkMode = theme.state.darkMode;
    
  return (
    <div className="intro">
        <div className="i-left">
            <div className="i-name">
                <span style={{color:darkMode?'white':''}}>Hy! I Am </span>
                <span>Ehthisham</span>
                <span>As a web developer, my object ive is to create user-friendly and efficient websites that are visually appealing and easy to navigate. I utilize my skills in HTML, CSS, JavaScript, and PHP to develop websites that meet all the requirements of the client. </span>
            </div>
            <button className='button i-button'>
                Hire Me
            </button>
            <div className="i-icon">
                <a href="#">
                    <img src={Github} alt="Github" />
                </a>
                <a href="#">
                    <img src={Linked} alt="Linked" />
                </a>
                <a href="#">
                    <img src={Instagram} alt="Instagram" />
                </a>
            </div>
        </div>
        <div className="i-right">
            <img src={Vector1} alt="v1" />
            <img src={Vector2} alt="V2" />
            <img src={Boy} alt="boy" />
            <motion.img
                initial={{left:'-36%'}}
                whileInView={{left:'-24%'}}
                transition={{trasition}}
            src={glassesimoji} alt="Emoji" />
            <motion.div
                initial={{top:'-4%', left:'74%'}}
                whileInView={{left:'68%'}}
                transition={{trasition}}   
                className='floatingdiv'                          
                // style={{top:'-4%' , left:'68%'}}
                >
                <Floadtingdiv image={crown} txt1='Web' txt2='Developer'/>
            </motion.div>
            <motion.div 
                initial={{left:'9rem', top:'18rem'}}
                whileInView={{left:'1rem'}}
                transition={{trasition}}                                
                className='floatingdiv'
                // style={{top:'18rem' , left:'1rem'}}
                >
                <Floadtingdiv image={thumbup} txt1='Best Design' txt2='Award'/>
            </motion.div>
            <div className='blur' style={{background: 'rgb(238,210,2545)'}}></div>
            <div className='blur' style={{background: '#C1F5FF', top:'17rem', width:'21rem', height:'11rem', left:'-9rem'}}></div>
        </div>
    </div>
  )
}

export default Intro