import React from 'react'
import './Services.css'

import { themeContext } from "../../Context";
import { useContext } from "react";

import { motion } from 'framer-motion';

import Heartemoji from '../../img/heartemoji.png'
import Glass from '../../img/glasses.png'
import Humble from '../../img/humble.png'
import Cards from '../Cards/Cards'

const Services = () => {

    const trasition = {duration:2, type:'spring;'}
    
    const theme = useContext(themeContext);
    const darkMode = theme.state.darkMode;

  return (
    <div className="services" id='Services'>
        {/* left */}
        <div className="awesome">
            <span style={{color:darkMode?'white':''}}>My Awesome</span>
            <span>services</span>
            <span>Lorem ipsum dolor sit amet consectetur adipisicing elit. Quidem fugit eos, distinctio magni amet 
            <br/>
            maiores nostrum odio iusto atque perspiciatis, maxime beatae?</span>
            <button className='button s-button'>Download CV</button>
            <div className="blur s-blur1" style={{background:'#ABF1FF94'}}></div>
        </div>
        {/* right */}
        <div className="cards">
            <motion.div 
            initial={{left:'22rem'}}
            whileInView={{left:'14rem'}}
            transition={{trasition}}
            // style={{left:'14rem'}}
            >
                <Cards
                    emoji={Heartemoji}
                    heading={'Design'}
                    details = {"Figma, Sktech, PhotoShop,Adobe, Adobe xd"}
                />
            </motion.div>

            <motion.div 
                initial={{top:'12rem', left:'-10rem'}}
                whileInView={{left:'-4rem'}}
                transition={{trasition}}                   
                
                // style={{top:'12rem', left:'-4rem'}}
                >
                <Cards
                    emoji={Glass}
                    heading={'Developer'}
                    details = {"VBA, HTML, CSS,Javascript, React js, SQL , Mongo DB"}
                />
            </motion.div>     


            <motion.div 
                initial={{left:'25rem', top:'19rem'}}
                whileInView={{left:'12rem'}}
                transition={{trasition}}                                  
            
            // style={{top:'19rem', left:'12rem'}}
            >
                <Cards
                    emoji={Humble}
                    heading={'Database'}
                    details = {"My SQL, Access, Mongo DB, Postgre Sql, SQL Server, Amazon S3"}
                />
            </motion.div> 
            <div className="blur2 s-blur2" style={{background:'var(--purple)'}}></div>                       

        </div>        
        

    </div>
  )
}

export default Services